import React, { useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";

const PropertyGrid = function (props, ref) {
    return view(props, ref);
}

const view = (props, ref) => {
    const element = props.element;
    return (<div>
        <div>
            <div>Name: {element.name}</div>
            <div>Width: {element.width}</div>
            <div>Height: {element.height}</div>
        </div>
    </div>);
}

const props = [];

const WrappedComponent = wrapper(React.forwardRef(PropertyGrid), props);
export { PropertyGrid, WrappedComponent };